.text{width:100%;display:flex;justify-content:center;align-items:center}.text h2{font-weight:500 !important}.imgs{overflow:hidden;position:relative;width:100%}.imgs img{width:100%;height:250px;object-fit:cover}.imgs::before{content:"↷";position:absolute;top:50%;left:50%;width:60px;height:60px;border-radius:50%;padding:30px;color:#fff;font-size:40px;background-color:rgba(105,177,77,.5);transform:translate(-50%, 160%);transition:transform .3s ease;z-index:1000;pointer-events:none;display:flex;align-items:center;justify-content:center}.imgs::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.5);transform:translateY(100%);transition:transform .3s ease;z-index:1;pointer-events:none}.imgs:hover::after{background-color:rgba(0,0,0,.5);transform:translateY(0)}.imgs:hover::before{transform:translate(-50%, -50%)}.top-absolute:nth-of-type(1){position:absolute;cursor:pointer;top:20px;right:20px;font-size:20px;background-color:rgba(0,0,0,0);z-index:20;border:none}.top-absolute:nth-of-type(1):hover{color:rgba(0,0,0,.4)}.top-absolute:nth-of-type(2){position:absolute;cursor:pointer;top:20px;right:40px;padding-right:10px;font-size:20px;background-color:rgba(0,0,0,0);color:#000;z-index:20;border:none}.top-absolute:nth-of-type(2):hover{color:rgba(0,0,0,.3)}.report{position:"absolute !important";background-color:#333;color:#fff;top:0px;right:0px;padding:10px 20px}.report:hover{background-color:#666}